    <h5>What I do as a Computing Education researcher:</h5>
    <br>
    <p>
      Despite the growing national interest in recruiting more computer science students, computer science remains a
      subject that contains <a href="https://www.pnas.org/doi/full/10.1073/pnas.2100030118">stereotypes</a>,
      <a href="https://dl.acm.org/doi/abs/10.1145/3501385.3543963">gender bias</a>,
