      who perceive computer science as being <a href="https://www.sciencedirect.com/science/article/pii/S0360131506001953"> "boring and irrelevant" </a>.

    </p>

    <p>

      My research goal is to help novice students see computing as a tool they could use to pursue their personal ideas and interests,
      and build things that are <i>interesting, meaningful, and creative</i> to them.

    </p>

    <p>In specific, my research focus on the following two areas:</p>
    <p><strong>1. Data-driven, automated feedback for programming practice assignments.</strong> Students can easily get stuck and feel frustrated when working on a programming assignment. I have built multiple tools to help students feel motivated and progress through programming practices:</p>
    <ul>
      <li><a href="https://emmableu.github.io/publications/wang2020crescendo.pdf">Crescendo</a>, a self-paced programming practice environment that combines the block-based and visual, interactive programming of Snap.</li>
      <li><a href="https://emmableu.github.io/publications/wang2020step.pdf">Step Tutor</a>, a tool that helps struggling students during programming by presenting them with relevant, step-by-step examples. </li>
      <li><a href="https://emmableu.github.io/publications/wang2021snapcheck.pdf">SnapCheck</a>, which conducts dynamic testing to Snap programs.</li>
      <li><a href="https://emmableu.github.io/publications/wang2021execution.pdf">Execution-Trace-Based Feature Engineering Approach</a>, which extracts sequential patterns from execution traces to perform code classification and feedback generation tasks. </li>
    </ul>
    <br>
    <p><strong>2. Example support for creative, open-ended programming.</strong> The second part of my work focus on how to use code examples to support open-ended programming. </p>
    <ul>
      <li>My work on <a href="https://emmableu.github.io/publications/wang2021novices.pdf">Novices&#39; Learning Barriers When Using Code Examples in Open-Ended Programming</a> investigated the challenges of example use during open-ended programming.</li>
      <li>My work on <a href="https://emmableu.github.io/publications/wang2022exploring.pdf">Exploring Design Choices to Support Novices&#39; Example Use During Creative Open-Ended Programming</a> presents the design of a remodeled system Example Helper, encouraging students to test and modify examples before integrating them into their own code. </li>
      <li>I built a record-and-replay tool called <a href="https://emmableu.github.io/publications/wang2022pinpoint.pdf">Pinpoint</a>. Using Pinpoint, students can understand and reuse an existing program by isolating the code responsible for specific events during program execution.</li>
    </ul>
